*{
  box-sizing: border-box;
}

::-webkit-scrollbar{
  width: 7px;
  border-radius: 10px;
  margin: 0px;
  padding: 0px;
}

::-webkit-scrollbar-thumb{
  background-color: #333333c0;
  border-radius: 10px;
}



.main{
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-rows: 2fr 19fr;
  overflow: hidden;
}

.title{
  color: #333333;
  font-weight: 500;
  letter-spacing: 2px;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.search-results{
  overflow: hidden;
  display: grid;
  grid-template-rows: 3fr 10fr;
}

.search{
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}


.search img{
  height: 150px;
}

.query{
  margin-bottom: 35px;
}

.close{
  background: whitesmoke;
  font-weight: 500;
  font-family: monospace;
  font-size: 1.5em;
  border-radius: 50%;
  padding-bottom: 3px;
  height: 20px;
  width: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333333;
  position: relative;
  left: 125px;
  top: 25px;
  cursor: pointer;
}

.images{
  display: grid;
  grid-template-columns: repeat( auto-fill, minmax(150px, 2fr));
  grid-gap: 10px;
  overflow-y: scroll;
  padding: 10px;
  margin: 10px;
}


.images .tile{
  height: 150px;
  width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.images img{
  height: 150px;
  width: 150px;
}

.upload{
  display: flex;
  justify-content: center;
  align-items: center;
}

.upload input{
  display: none;
  height: 0px;
  width: 0px;
}

.upload label{
  margin-bottom: 50px;
  cursor: pointer;
}

label svg{
  height: 100px;
}